import { ChangeDetectionStrategy, Component, Inject, OnInit } from '@angular/core';
import { AuthService } from '@presentation/auth/auth.service';
import { OauthLoginService } from '@presentation/auth/components/oauth-login/oauth-login.service';
import { Subscriptions } from '@core/decorators/subscriptions.decorator';
import { Observable, of, Subscription } from 'rxjs';
import { UserService } from '@core/services/user.service';
import { mapTo, switchMap } from 'rxjs/operators';

@Component({
  selector: 'app-oauth-login-success',
  templateUrl: './oauth-login-success.component.html',
  styleUrls: [ './oauth-login-success.component.less' ],
  changeDetection: ChangeDetectionStrategy.OnPush
})
export class OauthLoginSuccessComponent implements OnInit {
  @Subscriptions()
  private _subs: Subscription;

  constructor(
    private $oauthLogin: OauthLoginService,
    private $auth: AuthService,
    private $user: UserService,
  ) {
  }

  ngOnInit(): void {
    if (!this.$oauthLogin.isOauthActive) {
      this.$oauthLogin.goToRootAuth();
      return;
    }

    this._subs = this.$auth
      .login('')
      .pipe(
        switchMap(({ success, data }) => {
          if (success) {
            return this.loginSuccess(data);
          }

          return of(false);
        })
      )
      .subscribe((result) => {
      });
  }

  private loginSuccess(data: Record<string, any>): Observable<boolean> {
    this.$user.setUser(data.labelId, data.deviceId, data.uuid, '');
    this.$auth.setLogin(true);
    this.$user.isOauthLoggedIn = true;

    return this.$auth
      .initializeSettings()
      .pipe(mapTo(true));
  }

  /*
    private loginError(): void {

    }*/
}
