import { CommonModule } from '@angular/common';
import { NgModule } from '@angular/core';
import { PasswordFieldModule } from '@base/password-field/password-field.module';
import { CoreModule } from '@core/core.module';
import { AuthRoutingModule } from './auth-routing.module';
import { ContainerComponent } from './components/container/container.component';
import { LoginComponent } from './components/login/login.component';
import { OtpLoginComponent } from './components/otp-login/otp-login.component';
import { OauthLoginComponent } from './components/oauth-login/oauth-login.component';
import { DictionaryService } from '@core/services/dictionary.service';
import { AuthDictionary } from '@presentation/auth/auth.dictionary';
import { OAuthModule } from 'angular-oauth2-oidc';
import { OauthLoginSuccessComponent } from './components/oauth-login/oauth-login-success/oauth-login-success.component';
import { OauthFinishComponent } from './components/oauth-login/oauth-finish/oauth-finish.component';
import { CaptchaModule } from '@base/captcha/captcha.module';
import { NzSpaceModule } from 'ng-zorro-antd/space';
import { OauthLoginService } from '@presentation/auth/components/oauth-login/oauth-login.service';
import { ReactiveFormsModule } from '@angular/forms';
import { TranslateModule } from '@ngx-translate/core';

@NgModule({
  declarations: [
    LoginComponent,
    OtpLoginComponent,
    ContainerComponent,
    OauthLoginComponent,
    OauthLoginSuccessComponent,
    OauthFinishComponent
  ],
  imports: [
    CommonModule,
    AuthRoutingModule,
    CoreModule.forChild(),
    PasswordFieldModule,
    OAuthModule.forRoot(),
    NzSpaceModule,
    CaptchaModule,
    ReactiveFormsModule,
    TranslateModule,
  ],
  providers: [OauthLoginService],
})
export class AuthModule {
  constructor() {
    DictionaryService.set(AuthDictionary);
  }
}
