import { NgModule } from '@angular/core';
import { RouterModule, Routes, ROUTES } from '@angular/router';
import { AuthService } from './auth.service';
import { LoginComponent } from './components/login/login.component';
import { OtpLoginComponent } from './components/otp-login/otp-login.component';
import { OauthLoginSuccessComponent } from '@presentation/auth/components/oauth-login/oauth-login-success/oauth-login-success.component';

@NgModule({
  imports: [ RouterModule ],
  exports: [ RouterModule ],
  providers: [ {
    provide: ROUTES,
    useFactory: configAuthMethodHandlerRoutes,
    deps: [ AuthService ],
    multi: true,
  } ],
})
export class AuthRoutingModule {
}

export function configAuthMethodHandlerRoutes($auth: AuthService): Routes {
  let routes: Routes;
  switch ($auth.authMethod) {
    case 'otp':
      return [ {
        path: '',
        component: OtpLoginComponent,
      } ];

    default:
      routes = [ {
        path: '',
        component: LoginComponent,
      } ];
  }

  if ($auth.oauth2Available) {
    routes.push({
      path: $auth.oauthSuccessRoute,
      component: OauthLoginSuccessComponent
    });
  }

  return routes;
}
