import { ChangeDetectionStrategy, Component, ElementRef, OnDestroy, ViewChild } from '@angular/core';
import { Subscriptions } from '@core/decorators/subscriptions.decorator';
import { UserService } from '@core/services/user.service';
import { BehaviorSubject, Subscription } from 'rxjs';
import { AuthService } from '../../auth.service';

@Component({
  selector: 'app-otp-login',
  templateUrl: './otp-login.component.html',
  styleUrls: ['./otp-login.component.less'],
  changeDetection: ChangeDetectionStrategy.OnPush,
})
export class OtpLoginComponent implements OnDestroy {
  authKey = '';
  authCode = '';
  error = new BehaviorSubject<boolean>(false);
  loading = new BehaviorSubject<boolean>(false);
  codeSent = new BehaviorSubject<boolean>(false);

  @ViewChild('authCodeInput') authCodeInput: ElementRef | undefined;

  @Subscriptions()
  sub: Subscription;

  constructor(private $auth: AuthService, private _user: UserService) {}

  public requestCodeAction(): void {
    this.loading.next(true);
    const credentials = AuthService.base64credentials('replicator', 'replicator');

    this.sub = this.$auth
      .getOtpCode(this.authKey, credentials)
      .subscribe(({ success, data }) => {
        if (success) {
          this.codeSent.next(true);
          this.setFocusOnAuthMethodInput();
        } else {
          this.error.next(true);
        }
        this.loading.next(false);
      });
  }

  public loginAction(): void {
    this.loading.next(true);

    this.sub = this.$auth
      .checkOtpCode(this.authKey, this.authCode)
      .subscribe(({ success, data }) => {
        if (success) {
          this.codeSent.next(true);
          this._user.setUser(data.token.labelId, data.token.deviceId, data.token.uuid, '');
          this.$auth.setLogin(true);
          this.$auth.initializeSettings().subscribe(() =>
            this.error.next(false)
          );
        } else {
          this.error.next(true);
        }
        this.loading.next(false);
      });
  }

  public requestNewCodeAction(): void {
    this.clearFields();
    this.codeSent.next(false);
  }

  private clearFields(): void{
    this.authCode = '';
  }

  private setFocusOnAuthMethodInput(): void{
    setTimeout(() => {
      if (this.authCodeInput) {
        this.authCodeInput.nativeElement.focus();
      }
    }, 0);
  }

  public ngOnDestroy() {}
}
