import { ChangeDetectionStrategy, Component, OnInit } from '@angular/core';
import { OauthLoginService } from '@presentation/auth/components/oauth-login/oauth-login.service';

@Component({
  selector: 'app-oauth-login',
  templateUrl: './oauth-login.component.html',
  styleUrls: [ './oauth-login.component.less' ],
  changeDetection: ChangeDetectionStrategy.OnPush
})
export class OauthLoginComponent implements OnInit {

  constructor(
    private $service: OauthLoginService
  ) {
  }

  loginAction(): void {
    this.$service.loginOauth();
  }

  ngOnInit(): void {
    this.$service.configureOauth();
  }
}
